@import '../../../styles/variables.scss';
@import '../../../styles/mixins.scss';
@import '../../../styles/placeholders.scss';

.nav {
  @include sizesWH(100%, 100%);
  display: none;
  width: auto;
  margin-right: auto;
  margin-left: 72px;

  @include responsive(desktop) {
    display: block;
  }

  &_list {
    @extend %items-center;
    list-style-type: none;
    text-transform: capitalize;

    &_item {
      &:last-child {
        text-transform: uppercase;
      }
    }

    &_item > a {
      @include sizesWH(100%, 100%);
      @include item_hover($bg-10-hover-color, $button2-bg-color-green);
      display: block;
      padding: 8px 12px;
      background-color: transparent;
    }
  }
}
